


















































































import {Component, Vue} from "vue-property-decorator";
import {ECommerce} from "@/types/form/ECommerce";

@Component
export default class FormOne extends Vue {
  form = {ecs: [], budget: null, dateRange: [], brand: ""};

  selectedEcOne = "";
  selectedEcTwo = "";
  selectedEcThree = "";

  ecs: ECommerce[] = [
    {label: "momo購物網", value: "momo"},
    {label: "PChome 24h購物", value: "pchome"},
    {label: "ETMall東森購物網", value: "etmall"},
  ];

  get ecOptionsOne(): ECommerce[] {
    return this.ecs;
  }

  get ecOptionsTwo(): ECommerce[] {
    return this.ecs.filter((ec) => this.selectedEcOne !== ec.value);
  }

  get ecOptionsThree(): ECommerce[] {
    const selected = [this.selectedEcOne, this.selectedEcTwo];
    return this.ecs.filter((ec) => selected.every((select) => select !== ec.value));
  }

  submit(): void {
    this.$router.push({name: "Form3"});
  }

  goBack(): void {
    this.$router.push({name: "Form1"});
  }
}
